const code =
`<template>
  <div class="example-cell-basic-usage">
    <vui-card icon="list-settings-filled" title="选项" borderless padding="0px">
      <vui-cell-group title="单元格">
        <vui-cell>标题文字</vui-cell>
        <vui-cell>标题文字</vui-cell>
      </vui-cell-group>
      <vui-cell-group title="带图标的单元格">
        <vui-cell>
          标题文字
          <vui-icon slot="icon" type="stack" />
        </vui-cell>
        <vui-cell>
          标题文字
          <vui-icon slot="icon" type="message-ellipsis" />
        </vui-cell>
      </vui-cell-group>
      <vui-cell-group title="带额外内容的单元格">
        <vui-cell>
          标题文字
          <template slot="extra">额外内容</template>
        </vui-cell>
        <vui-cell>
          标题文字
          <template slot="extra">额外内容</template>
        </vui-cell>
      </vui-cell-group>
      <vui-cell-group title="带图标、额外内容的单元格" description="我是分组底部说明文字">
        <vui-cell>
          标题文字
          <vui-icon slot="icon" type="stack" />
          <template slot="extra">额外内容</template>
        </vui-cell>
        <vui-cell>
          标题文字
          <vui-icon slot="icon" type="message-ellipsis" />
          <template slot="extra">额外内容</template>
        </vui-cell>
      </vui-cell-group>
    </vui-card>
  </div>
</template>

<script>
  export default {

  };
</script>

<style>
  .example-cell-basic-usage { background-color:#ececec; padding:20px; }
  .example-cell-basic-usage > .vui-card { width:300px; }
</style>
`;

export default code;


